<template>
    <div class="loginPage page_frame">
        <div class="page_head">
            <h1 v-if="isLogin"></h1>
            <h1 v-else></h1>
            <p></p>
        </div>
        <div class="page_content">
            <template v-if="isLogin">
                <div class="formItem">
                    <label>
                        <span></span>
                        <input type="text" placeholder="" v-model.trim="login.name">
                    </label>
                </div>
                <div class="formItem">
                    <label>
                        <span></span>
                        <input type="password" placeholder="" v-model.trim="login.key">
                    </label>
                </div>
                <div class="subCode">
                    <button @click="loginToSys"></button>
                </div>
                <div class="askToReg">
                    <span></span>
                    <a href="javascript:void(0)" @click="changeType('sign')"></a>
                </div>
                <div class="showSample">
                    <p>
                        fu 123456 <br/>
                        <b></b> <a href="mailto://aderx@qq.com">aderx@qq.com</a>
                    </p>
                </div>
            </template>
            <template v-else>
                <div class="formItem">
                    <label>
                        <span></span>
                        <input type="text" placeholder="20" v-model.trim="register.name">
                    </label>
                </div>
                <div class="formItem">
                    <label>
                        <span></span>
                        <input type="text" placeholder="6-10" v-model.trim="register.key">
                    </label>
                </div>
                <div class="formItem">
                    <label>
                        <span></span>
                        <input type="text" placeholder="" v-model.trim="register.email">
                    </label>
                </div>
                <div class="subCode">
                    <button @click="signToSys"></button>
                </div>
                <div class="askToReg">
                    <span></span>
                    <a href="javascript:void(0)" @click="changeType('log')"></a>
                </div>
            </template>
        </div>
        <msg
                :config="messageBox"
        />
    </div>
</template>

<script>
    export default {
        name: "login",
        data: function () {
            return {
                isLogin: true,
                login:{
                    name:"",
                    key:""
                },
                register:{
                    name:"",
                    key:"",
                    email:""
                },
                messageBox:{
                    show:false
                }
            }
        },
        methods: {
            changeType(t) {
                this.isLogin = (t === "log");
            },
            loginToSys(){
                //
                if(this.login.name === "" || this.login.key === ""){
                    this.messageBox = {
                        show:true,
                        msg:"",
                        cancelText:"",
                        name:"errBox"
                    };
                    return false;
                }else{
                    let that = this;
                    this.$fetch(
                        'login',
                        JSON.stringify(this.login),
                        (d)=>{
                        if(d.code === 200){
                            //
                            this.$store.login = {name:d.data.name,uId:d.data.id};
                            d.data.dTime ? this.$store.dTime = d.data.dTime:'';
                            this.messageBox = {
                                show:true,
                                msg:"",
                                cancelText:"",
                                name:"okBox",
                                func(){
                                    //
                                    that.login.name = "";
                                    that.login.key = "";
                                    setTimeout(()=>{
                                        //
                                        that.$parent.hidePage(function(){
                                            //
                                            this.loginInfo = this.$store.login;
                                        });
                                    },500);
                                }
                            };

                        }else{
                            this.messageBox = {
                                show:true,
                                msg:"",
                                cancelText:"",
                                name:"errBox",
                            };
                        }
                    }
                    )
                    .catch((e)=>{
                        console.error(e);
                        this.messageBox = {
                            show:true,
                            msg:"",
                            cancelText:"",
                            name:"errBox",
                        };
                    })
                }
            },
            signToSys(){
                if(this.register.name === "" || this.register.key === "" || this.register.email === ""){
                    this.messageBox = {
                        show:true,
                        msg:"",
                        cancelText:"",
                        name:"errBox"
                    };
                    return false;
                }else if(!/^([a-zA-Z0-9]|[_]){6,10}$/.test(this.register.key)){
                    this.messageBox = {
                        show:true,
                        msg:"610",
                        cancelText:"",
                        name:"errBox"
                    };
                    return false;
                }else{
                    let that = this;
                    this.$fetch(
                        'sign',
                        JSON.stringify(this.register),
                        (data)=>{
                            if(data.code === 200){
                                this.messageBox = {
                                    show:true,
                                    msg:"",
                                    cancelText:"",
                                    name:"okBox",
                                    func(){
                                        //
                                        that.register.name = "";
                                        that.register.key = "";
                                        that.register.email = "";
                                        setTimeout(()=>{
                                            //
                                            that.isLogin = true;
                                        },500);
                                    }
                                };
                            }else{
                                this.messageBox = {
                                    show:true,
                                    msg:data.msg,
                                    cancelText:"",
                                    name:"errBox",
                                };
                            }
                        }
                    )
                        .catch((e)=>{
                            console.error(e);
                            this.messageBox = {
                                show:true,
                                msg:"",
                                cancelText:"",
                                name:"errBox",
                            };
                        })
                }
            }
        }
    }
</script>

<style scoped>
    .log_content {
        margin-top: 30px;
    }

    .subCode button {
        width: 100%;
        height: 40px;
        margin-top: 20px;
    }

    .askToReg {
        margin-top: 30px;
        text-align: center;
    }
</style>