<template>
    <div class="addDataSource page_frame">
        <div class="page_head">
            <h1></h1>
            <p></p>
        </div>
        <div class="page_content">
            <div class="dividerBar">
                <span></span>
            </div>
            <div class="formItem">
                <label>
                    <span></span>
                    <input type="text" placeholder="16" v-model.trim="dataSource.info.name">
                </label>
            </div>
            <div class="formItem">
                <label>
                    <span></span>
                    <input type="text" placeholder="" v-model.trim="dataSource.info.desc">
                </label>
            </div>
            <div class="formItem bigSize">
                <label>
                    <span>()</span>
                    <input type="text" class="smallInput" placeholder="" v-model.trim="dataSource.limit.min">
                    -
                    <input type="text" class="smallInput" placeholder="" v-model.trim="dataSource.limit.max">
                </label>
            </div>
            <div class="formItem bigSize">
                <label>
                    <span></span>
                    <input type="text" placeholder="http(s)://" v-model.trim="dataSource.info.link">
                </label>
            </div>
            <div class="showSample">
                <p>
                    <br/>
                </p>
            </div>
            <div class="dividerBar">
                <span></span>
            </div>
            <div class="formItem">
                <label>
                    <span></span>
                    <input type="text" placeholder="" v-model.trim="dataSource.info.icon">
                </label>
            </div>
            <iconSelect @getIcon="getIcon" />
            <div class="showSample">
                <p>
                    ICON  <a href="https://fontawesome.com/icons?d=gallery&s=solid" target="_blank"></a>  
                </p>
            </div>
            <div class="formItem" style="position: relative;">
                <label>
                    <span></span>
                    <input type="text"  placeholder="" v-model.trim="dataSource.info.color">
                    <span class="showColorBox" :style="'background:'+dataSource.info.color"></span>
                </label>
            </div>
            <colorSelect @getColor="getColor"/>
            <div class="dividerBar">
                <span></span>
            </div>
            <div class="formItem bigSize">
                <span></span>
                <radio :status="dataSource.option.showAllUser" name="showAllUser" @changeStatus="changeRadio"/>
            </div>
            <div class="showSample">
                <p>
                    <b style="color:#f10214;"></b><br>
                    <br>
                    
                </p>
            </div>
            <div class="formItem bigSize">
                <span style="width:65%;"></span>
                <radio :status="dataSource.option.line" name="line" @changeStatus="changeRadio"/>
            </div>
            <div class="formItem bigSize">
                <label>
                    <span>m</span>
                    <input type="text" placeholder="" v-model.trim="dataSource.option.area">
                </label>
            </div>
            <div class="showSample">
                <p>
                    <b style="color:#f10214;"></b><br>
                    <br />
                    <br>
                    <br>
                    
                </p>
            </div>
            <div class="subCode">
                <button @click="submitData"></button>
            </div>
        </div>
        <msg
            :config="messageBox"
        />
    </div>
</template>

<script>
    import colorSelect from "../components/colorSelect";
    import iconSelect from "../components/iconSelect";

    export default {
        name: "add",
        data() {
            return {
                basic:{
                    moreColor:false
                },
                dataSource: {
                    info:{
                        name:"",
                        desc:"",
                        link:"",
                        icon:"",
                        color:"#1890ff"
                    },
                    limit:{
                        min:'',
                        max:''
                    },
                    option:{
                        showAllUser:true,
                        line:true,
                        area:''
                    }
                },
                messageBox:{
                    show:false
                }
            }
        },
        methods: {
            changeRadio(val, name) {
                this.dataSource.option[name] = val;
            },
            submitData(){
                let d= this.dataSource;
                if(!d.info.name || !d.info.desc){
                    this.messageBox = {
                        show:true,
                        cancelText:"",
                        name:"basicBox",
                        msg:""
                    }
                }else if(!d.info.icon || !d.info.color){
                    this.messageBox = {
                        show:true,
                        cancelText:"",
                        name:"ExpandBox",
                        msg:""
                    }
                } else{
                    if(!d.option.area){
                        this.messageBox = {
                            show:true,
                            cancelText:"",
                            name:"areaBox",
                            msg:""
                        }
                    }else{
                        d.uId = this.$store.login.uId;
                        if(isNaN(Number(d.limit.min)) || d.limit.min === '' || d.limit.min<-999)d.limit.min = -Infinity;
                        if(isNaN(Number(d.limit.max)) || d.limit.max === '' || d.limit.max>-999)d.limit.max = Infinity;
                        this.$fetch(
                            'envAdd',
                            JSON.stringify(d),
                            d=>{
                                if(d.code === 200){
                                    this.messageBox = {
                                        show:true,
                                        cancelText:"",
                                        name:"okBox",
                                        msg:""
                                    };
                                    this.dataSource = {
                                        info:{
                                            name:"",
                                            desc:"",
                                            link:"",
                                            icon:"",
                                            color:"#1890ff"
                                        },
                                        limit:{
                                            min:'',
                                            max:''
                                        },
                                        option:{
                                            showAllUser:true,
                                            line:true,
                                            area:''
                                        }
                                    }
                                }else{
                                    this.messageBox = {
                                        show:true,
                                        cancelText:"",
                                        name:"errBox",
                                        msg:""
                                    }
                                }
                            }
                        )
                        .catch(e=>{
                            this.messageBox = {
                                show:true,
                                cancelText:"",
                                name:"errBox",
                                msg:""
                            };
                            console.log(e);
                        })
                    }
                }

            },
            getColor(color){
                this.dataSource.info.color = color;
            },
            getIcon(name){
                this.dataSource.info.icon = name;
            }
        },
        components:{
            colorSelect,
            iconSelect
        }
    }
</script>

<style scoped>
    .showColorBox{
        width:10px;
        height:10px;
        background:#fff;
        position: absolute;
        left:25%;
        top:38%;
        overflow: hidden;
    }

</style>