<template>
    <div class="aboutMe page_frame">
        <div class="page_head">
            <h1></h1>
            <p></p>
        </div>
        <div class="page_content">
            <template v-if="envInfo.envId && envInfo.uId === $store.login.uId">
                <div class="dividerBar">
                    <span></span>
                </div>
                <div class="formItem">
                    <label>
                        <span></span>
                        <input type="text" placeholder="" v-model.trim="envInfo.info.name">
                    </label>
                </div>
                <div class="formItem">
                    <label>
                        <span></span>
                        <input type="text" placeholder="" v-model.trim="envInfo.info.desc">
                    </label>
                </div>
                <div class="moreBtn" v-show="!moreMsg" @click="()=>{this.moreMsg = true;}"></div>
                <div class="moreBtn" v-show="moreMsg" @click="()=>{this.moreMsg = false;}"></div>
                <div class="showMore" v-show="moreMsg">
                    <div class="formItem bigSize">
                        <label>
                            <span>()</span>
                            <input type="text" class="smallInput" placeholder="" v-model.trim="envInfo.limit.min">
                            -
                            <input type="text" class="smallInput" placeholder="" v-model.trim="envInfo.limit.max">
                        </label>
                    </div>
                    <div class="formItem bigSize">
                        <label>
                            <span></span>
                            <input type="text" placeholder="http(s)://" v-model.trim="envInfo.info.link">
                        </label>
                    </div>
                    <div class="formItem">
                        <label>
                            <span></span>
                            <input type="text" placeholder="FontAwesome" v-model.trim="envInfo.info.icon">
                        </label>
                    </div>
                    <iconSelect  @getIcon="getIcon"/>
                    <div class="formItem" style="position: relative;">
                        <label>
                            <span></span>
                            <input type="text"  placeholder="" v-model.trim="envInfo.info.color">
                            <span class="showColorBox" :style="'background:'+envInfo.info.color"></span>
                        </label>
                    </div>
                    <colorSelect @getColor="getColor"/>
                    <div class="formItem bigSize">
                        <span></span>
                        <radio :status="envInfo.option.showAllUser" />
                    </div>
                    <div class="formItem">
                        <span></span>
                        <radio :status="envInfo.option.line" />
                    </div>
                    <div class="formItem bigSize">
                        <label>
                            <span>m</span>
                            <input type="text" placeholder="" v-model.trim="envInfo.option.area">
                        </label>
                    </div>
                    <div class="show_btn">
                        <button class="back_btn" @click="saveEnv"></button>
                    </div>
                    <div class="show_btn">
                        <button class="del_btn" @click="delEnv"></button>
                    </div>
                </div>
            </template>
            <div class="dividerBar">
                <span></span>
            </div>
            <div class="show_btn">
                <button class="back_btn" @click="backEnv"></button>
            </div>
            <template v-if="envInfo.envId">
                <div class="dividerBar">
                    <span></span>
                </div>
                <div class="formItem">
                    <span style="width:60%;"></span>
                    <radio :status="sort === 'runFirst'" name="first" @changeStatus="changeStatus" />
                </div>
                <div class="formItem">
                    <span style="width:60%;"></span>
                    <radio :status="showAve" name="ave" @changeStatus="changeStatus" />
                </div>

                <div class="formItem">
                    <span style="width:60%;"></span>
                    <radio :status="reFresh" name="fresh" @changeStatus="changeStatus" />
                </div>
                <div class="formItem bigSize">
                    <span></span>
                    <radio :status="tempAlert" name="alert" @changeStatus="changeStatus" />
                </div>
                <div class="formItem bigSize">
                    <span></span>
                    <radio :status="tempLog" name="log" @changeStatus="changeStatus" />
                </div>
                <div class="show_btn">
                    <button class="back_btn" @click="openPage('log')"></button>
                </div>
            </template>
        </div>
        <msg
            :config="messageBox"
            @action="checkBox"
        />
    </div>
</template>

<script>
    import colorSelect from "../components/colorSelect";
    import iconSelect from "../components/iconSelect";
    export default {
        name: "about",
        props:["envInfo","sort","reFresh","tempLog","showAve","tempAlert"],
        data(){
            return {
                messageBox:{
                    show:false
                },
                moreMsg:false,
                loginInfo:false
            }
        },
        computed:{
            getTime(){
                return this.time/1000;
            }
        },
        methods:{
            changeStatus(status,name){
                let set = {
                    dSort:this.sort,
                    showAve:this.showAve,
                    reFresh:this.reFresh,
                    tempAlert:this.tempAlert,
                    tempLog:this.tempLog
                };

                this.messageBox = {
                    show:true,
                    cancelText:"",
                    name:name,
                    msg:""
                };
                if(name === "fresh"){
                    set.reFresh = status;
                    this.$emit('changeVal','reFresh',status,false,true);
                }else if(name === "first"){
                    set.dSort = status ? "runFirst":"default";
                    this.$emit('changeVal','dSort',status ? "runFirst":"default");
                }else if(name === "ave"){
                    set.showAve = status;
                    this.$emit('changeVal','showAve',status,false,true);
                }else if(name === "alert"){
                    set.tempAlert = status;
                    this.$emit('changeVal','tempAlert',status,false,true);
                }else if(name === "log"){
                    set.tempLog = status;
                    this.$emit('changeVal','tempLog',status,false,true);
                }

                localStorage.setItem('SETTING',JSON.stringify(set));
            },
            backEnv(){
                this.$router.push('/')
            },
            getColor(color){
                this.envInfo.info.color = color;
            },
            getIcon(name){
                this.envInfo.info.icon = name;
            },
            openPage(name){
                this.$parent.showPage(name);
            },
            saveEnv(){
                this.$fetch(
                    'envEdit',
                    JSON.stringify(this.envInfo),
                    d=>{
                        if(d.code === 200){
                            this.messageBox = {
                                show:true,
                                cancelText:"",
                                name:'okBox',
                                msg:""
                            };
                        }else{
                            this.messageBox = {
                                show:true,
                                cancelText:"",
                                name:'errBox',
                                msg:""
                            };
                        }

                    }
                )
            },
            delEnv(){
                this.messageBox = {
                    show:true,
                    showOk:true,
                    okText:"",
                    times:1,
                    msg : ""
                }
            },
            checkBox(val,name){
                if(val) {
                    if (this.messageBox.times === 2) {
                        this.messageBox = {
                            show: true,
                            showOk: true,
                            okText: "",
                            times: 2,
                            msg: "",
                        }
                    } else if (this.messageBox.times === 3) {
                        this.messageBox.show = false;
                        this.$fetch(
                            'envDel',
                            JSON.stringify({
                                uId:this.$store.login.uId,
                                envId:this.envInfo.envId
                            }),
                            d => {
                                if (d.code === 200) {
                                    let that = this;
                                    this.messageBox = {
                                        show: true,
                                        cancelText: "",
                                        name: "del",
                                        msg: "",
                                        func(){
                                            //
                                            that.$router.replace("/");
                                        }
                                    };
                                } else {
                                    this.messageBox = {
                                        show: true,
                                        cancelText: "",
                                        name: "delFail",
                                        msg: "",
                                    };
                                }
                            }
                        )
                    }
                }
            }
        },
        components:{
            colorSelect,
            iconSelect
        }
    }
</script>

<style scoped>

    .back_btn{
        width:100%;
        height:3rem;
    }

    .show_btn{
        margin-top:20px;
    }

    .showColorBox{
        width:10px;
        height:10px;
        background:#fff;
        position: absolute;
        left:25%;
        top:38%;
        overflow: hidden;
    }

    .moreBtn{
        width:100%;
        margin:10px 0;
        text-align: center;
        border:1px solid #c1c1c1;
        padding:5px;
        box-sizing: border-box;
        border-radius:10px;
    }

</style>